.mansonry{position:relative;-moz-columns:10;columns:10;gap:1.25rem
}
.mansonry:has(figure:hover) figure:hover figcaption{--tw-translate-y:0px;transform:translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));opacity:1
}
@media screen and (max-width:1720px){ .mansonry{-moz-column-count:9;column-count:9 }
}
@media screen and (max-width:1520px){ .mansonry{-moz-column-count:8;column-count:8 }
}
@media screen and (max-width:1420px){ .mansonry{-moz-column-count:7;column-count:7 }
}
@media screen and (max-width:1200px){ .mansonry{-moz-column-count:5;column-count:5 }
}
@media screen and (max-width:960px){ .mansonry{-moz-column-count:3;column-count:3 }
}
@media screen and (max-width:640px){ .mansonry{-moz-column-count:2;column-count:2 }
}
.mansonry figure{position:relative;margin:0px;margin-bottom:1.25rem;display:grid;cursor:pointer;-moz-column-break-inside:avoid;break-inside:avoid;overflow:hidden;padding:0px;transition-property:all;transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);transition-duration:500ms;grid-template-rows:1fr auto
}
.mansonry figure div{position:relative;height:100%;width:100%;overflow:hidden;grid-column:1;grid-row:1/-1
}
.mansonry figure div::after{pointer-events:none;position:absolute;bottom:0px;left:0px;z-index:50;height:0px;width:100%;background-image:linear-gradient(to top,#1119,#0000);--tw-saturate:saturate(3);filter:var(--tw-blur) var(--tw-brightness) var(--tw-contrast) var(--tw-grayscale) var(--tw-hue-rotate) var(--tw-invert) var(--tw-saturate) var(--tw-sepia) var(--tw-drop-shadow);transition-property:all;transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);transition-duration:100ms;--tw-content:"";content:var(--tw-content)
}
.mansonry figure div:hover::after{content:var(--tw-content);height:100%
}
.mansonry figure figcaption{pointer-events:none;position:absolute;bottom:0.75rem;left:1rem;z-index:50;height:100%;width:100%;--tw-translate-y:2rem;transform:translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));padding-left:1rem;padding-right:1rem;padding-top:1.25rem;padding-bottom:1.25rem;opacity:0;transition-property:all;transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);transition-duration:300ms;text-shadow:1px 0px 7px #000a
}
.mansonry figure:has(.loaded) img{ opacity:1 }
.mansonry figure:has(.loaded) .placeholder{ opacity:0 }
@keyframes scaling{ from{scale:1 }
to{ scale:1.2 }
}
@keyframes placehold{ 0%{background-position:-468px 0 }
100%{ background-position:468px 0 }
}
@font-face{ font-family:"Highland Jakarta"; src:url("/_next/static/media/HighJakarta.d1b3bbee.woff2") }
body{ background:#f0f0f0; font-family:system-ui,-apple-system,"Segoe UI","Roboto","Ubuntu","Cantarell","Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji"; position:relative; font-synthesis:none; text-rendering:optimizeLegibility; -webkit-font-smoothing:antialiased; -moz-osx-font-smoothing:grayscale; -webkit-text-size-adjust:100%; height:-moz-fit-content; height:fit-content; overflow-x:hidden; transition-duration:200ms; background:linear-gradient(to bottom,#e5e7ebcc,#e5e7ebcc),url(/_next/static/media/blurred.0b4acc7b.avif); interpolate-size:allow-keywords }
body:has(.fav){overflow:hidden
}
body{ background-attachment:fixed !important; background-size:cover !important }
.squircle{ border-radius:30% 70% 70% 30%/30% 30% 70% 70% }
@supports(corner-shape:squircle){ .squircle{corner-shape:squircle; border-radius:100px }
}
@view-transition{ navigation:auto }
[style*=view-transition-name][style*=figure-]{ contain:layout }
::view-transition-old(root){ animation:none }
::view-transition-new(root){ animation:none }
::view-transition-old(figure-caption){ animation:fadeOut .3s ease-out }
::view-transition-new(figure-caption){ animation:fadeIn .3s ease-in .1s }
@keyframes scaleUp{ from{scale:.5; opacity:.5 }
to{ scale:1; opacity:1 }
}
@keyframes scaleDown{ from{transform:scale(1); opacity:1 }
to{ transform:scale(0.5); opacity:0 }
}
@keyframes fadeOut{ to{opacity:0 }
}
@keyframes fadeIn{ from{opacity:0 }
to{ opacity:1 }
}